.section-artlist {
  width: 100%;
  height: 272px;
  &_title {
    display: inline-block;
    color: #800080;
    text-transform: uppercase;
    font-weight: 300;
    font-family: "Open Sans", "segoe ui", Helvetica, Arial, sans-serif;
    font-size: 26px;
  }
}
.artist-wapper {
  display: grid;
  grid-template-columns: 210px 438px;
  width: 100%;
  height: 210px;
}
